.mainCard {
  border-radius: 25px;
  width: clamp(400px, 80vw, 1200px);
  height: clamp(350px, 70vw, 900px);
  box-shadow: 1px 1px 5px 5px rgb(134, 134, 134);
  margin-top: 5%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: left;
}
